import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  moduleId: module.id,
  selector: 'radiogroup-input',
  templateUrl: 'radiogroup.demo.html'
})
export class RadioGroupDemoComponent implements OnInit {
  radioGroupData: any;
  tsCode:any;
  htmlCode: any;
  isExpanded : boolean=false;
  constructor() {
    this.radioGroupData={
      response:{
        data:[{
          gender:'Male',
          genderId:'male'
        },{
          gender:'Female',
          genderId:'female'
        },
        ]}};
    this.tsCode=`import {Component, OnInit} from '@angular/core';
@Component({
  selector: 'radiogroup-input',
  templateUrl: 'radiogroup.demo.html'
})
export class RadioGroupDemoComponent implements OnInit {
  radioGroupData: any;
  constructor() {
    this.radioGroupData={
      response:{
        data:[{
          gender:'Male',
          genderId:'male'
        },{
          gender:'Female',
          genderId:'female'
        },
        ]}}
  }

  ngOnInit() {
  }
}
    `
    this.htmlCode=`<!--Horizontal RadioGroup-->

<amexio-radio-group [fieldLabel]="'Horizontal RadioGroup'" name ="gender"
              [fieldName]="'gender'"
              [dataReader]="'response.data'"
              [displayField]="'gender'"
              [valueField]="'genderId'"
              [radioGroupBindData]="radioGroupData"
              (selectedValue)="setSelectedGender($event)"
              [column]="'6'">
</amexio-radio-group>

<!--Vertical RadioGroup-->

<amexio-radio-group [fieldLabel]="'Vertical RadioGroup'" name ="gender"
              [fieldName]="'gender'"
              [dataReader]="'response.data'"
              [displayField]="'gender'"
              [valueField]="'genderId'"
              [radioGroupBindData]="radioGroupData"
              (selectedValue)="setSelectedGender($event)">
</amexio-radio-group>
    `
  }

  ngOnInit() {
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



