import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'number-demo',
  templateUrl: 'number.demo.html'
})
export class NumberDemoComponent implements OnInit {
  tsCode:any;
  htmlCode:any;
  isExpanded :boolean=false;
  constructor() {
    this.htmlCode=`<!--Simple Number Input-->

<amexio-number-input [fieldLabel]="'Number Input'" name ="age"
                     [placeholder]="'Enter Age'">
</amexio-number-input>


<!--Text Input Without Label and Icon feedback-->

<amexio-number-input name ="age"
                   [placeholder]="'Enter age'"
                   [iconFeedBack]="false"
                   [hasLabel]="false">
</amexio-number-input>

<!--Text Input with Validations-->

<amexio-number-input  [fieldLabel]="'Age'" name ="age"
                    [placeholder]="'Enter age'"
                    [allowBlank]="false" [errorMsg] ="'Please enter age'"
                    [minValue]="1"
                    [minErrorMsg]="'age can not be less than 1'"
                    [maxValue]="100"  [maxErrorMsg]="'age can not be greater than 100'"
                    [iconFeedBack]="true"
                    [iconFeedBack]="true">
</amexio-number-input>

<!--Text Input disabled Property-->

<amexio-number-input name ="age" 
                   [fieldLabel]="'age'"
                   [placeholder]="'Enter age'"
                   [disabled]="true">
</amexio-number-input>

<!--Text Input with Font Style : italic-->

<amexio-number-input name ="age" 
                   [fieldLabel]="'age'"
                   [placeholder]="'Enter age'"
                   [fontStyle]="'italic'">
</amexio-number-input>

<!--Text Input with Font Family : Times New Roman and Font Size : large-->

<amexio-number-input name ="age" 
                   [fieldLabel]="'Age'"
                   [placeholder]="'Enter age'"
                   [fontFamily]="'Times New Roman'"
                   [fontSize]="'large'">
</amexio-number-input>

    
    `;
    this.tsCode=`import {Component, OnInit} from '@angular/core';
    
@Component({
  selector: 'number-demo',
  templateUrl: 'number.demo.html'
})
export class NumberDemoComponent implements OnInit {
  constructor() {
  }
  ngOnInit() {
  }
}

    `
  }

  ngOnInit() {
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



