import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'datagrid-demo',
  templateUrl: 'datagrid.demo.html'
})
export class DataGridDemo implements OnInit {

  htmlCode : string;
  tsCode : string;
  datasource : string;
  data : any[];
  clickedRowData : any;
  isExpanded : boolean=false;


  constructor() {
    this.htmlCode = `
<amexio-data-table
                [title]="'Data Grid'"
                [pageSize] = "10"
                [dataReader]="'response.data'"
                [checkboxSelect]="true"
                [httpUrl]="'assets/data/country.json'"
                [httpMethod]="'get'"
                (selectedRowData)="onRowSelect($event)"
                (rowSelect)="onRowClick($event)">
                <amexio-data-table-column [dataIndex]="'countryName'" [dataType]="'string'" [hidden]="false" [text]="'Name'"></amexio-data-table-column>
                <amexio-data-table-column [dataIndex]="'capital'" [dataType]="'string'" [hidden]="true" [text]="'Capital'"></amexio-data-table-column>
                <amexio-data-table-column [dataIndex]="'countryCode1'" [dataType]="'string'" [hidden]="false" [text]="'Code1'"></amexio-data-table-column>
                <amexio-data-table-column [dataIndex]="'countryCode2'" [dataType]="'string'" [hidden]="false" [text]="'Code2'"></amexio-data-table-column>
                <amexio-data-table-column [dataIndex]="'countryFlag'" [dataType]="'string'" [hidden]="false" [text]="'Flag'"></amexio-data-table-column>
                <amexio-data-table-column [dataIndex]="'isoNumeric'" [dataType]="'number'" [hidden]="false" [text]="'ISO'" [summaryCaption]="'Sum'" [summaryType]="'sum'">
                </amexio-data-table-column>
</amexio-data-table>`;


    this.tsCode = `import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'datagrid-demo',
  templateUrl: 'datagrid.demo.html'
})
export class DataGridDemo implements OnInit {
    constructor(){}
    ngOnInit(){}

}
    `;

  this.datasource = `{
  "response": {
    "success": true,
    "message": "Fetching  Data  Request Succeeded: Country",
    "data": [
      {
        "countryName": "U.S. Virgin Isssssss",
        "countryCode1": "VI",
        "countryCode2": "VIR",
        "countryFlag": "VI.png",
        "capital": "",
        "currencyCode": "USD",
        "currencyName": "Dollar",
        "currencySymbol": "$",
        "capitalLatitude": null,
        "capitalLongitude": null,
        "isoNumeric": 850,
        "primaryKey": "02818773-23CF-40E5-8AD6-0E07EBD3569A",
        "countryId": "02818773-23CF-40E5-8AD6-0E07EBD3569A",
        "versionId": 8,
        "systemInfo": {
          "activeStatus": 1,
          "txnAccessCode": 51000
        },
        "primaryDisplay": "U.S. Virgin Isssssss"
      },
      {
        "countryName": "Latvia",
        "countryCode1": "LV",
        "countryCode2": "LVA",
        "countryFlag": "LV.png",
        "capital": "",
        "currencyCode": "LVL",
        "currencyName": "Lat",
        "currencySymbol": "Ls",
        "capitalLatitude": null,
        "capitalLongitude": null,
        "isoNumeric": 428,
        "primaryKey": "03640A86-304D-40C8-AC82-3EB8509DF8C4",
        "countryId": "03640A86-304D-40C8-AC82-3EB8509DF8C4",
        "versionId": 2,
        "systemInfo": {
          "activeStatus": 1,
          "txnAccessCode": 51000
        },
        "primaryDisplay": "Latvia"
      },
      {
        "countryName": "British Indian Ocean Terr",
        "countryCode1": "IO",
        "countryCode2": "IOT",
        "countryFlag": "IO.png",
        "capital": "",
        "currencyCode": "USD",
        "currencyName": "Dollar",
        "currencySymbol": "$",
        "capitalLatitude": null,
        "capitalLongitude": null,
        "isoNumeric": 86,
        "primaryKey": "03ABE27B-E251-4524-9F88-BD07671144FE",
        "countryId": "03ABE27B-E251-4524-9F88-BD07671144FE",
        "versionId": 4,
        "systemInfo": {
          "activeStatus": 1,
          "txnAccessCode": 51000
        },
        "primaryDisplay": "British Indian Ocean Terr"
      }
      ]
  }
}`;


  }

  ngOnInit() {
  }

  onRowSelect(data : any){
    this.data = data;
  }

  onRowClick(data : any){
    this.clickedRowData = data;
  }

  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }

  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }

}



