import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  moduleId: module.id,
  selector: 'datetime-demo',
  templateUrl: 'datetime.html'
})
export class DateTimeDemoComponent implements OnInit {
  currentDate : Date = new Date(2016, 5, 10);
  time : Date = new Date();
  htmlCode : string;
  tsCode : string;
  isExpanded :boolean=false;
  datepickerOpts = {
    startDate: new Date(2016, 5, 10),
    autoclose: true,
    todayBtn: 'linked',
    todayHighlight: true,
    assumeNearbyYear: true,
    format: 'D, d MM yyyy'
  };

  constructor() {
    this.htmlCode = `
<!--Date Only -->
<amexio-date-time-picker 
       [timepicker]="false" 
       [datepicker]="datepickerOpts" 
       [(ngModel)]="date3">
</amexio-date-time-picker>

<!-- Time Picker Only -->
 <amexio-date-time-picker 
         [datepicker]="false" 
         [ngModel]="date1" 
         (ngModelChange)="onChangeTime($event)">
</amexio-date-time-picker>
`;
    this.tsCode = `import {Component, OnInit} from '@angular/core';

@Component({

  selector: 'datetime-demo',
  templateUrl: 'datetime.html'
})
export class DateTimeDemoComponent implements OnInit {
    currentDate : Date = new Date(2016, 5, 10);
    time : Date = new Date();
    constructor(){};
    ngOnInit(){};
    onChangeTime(event : any){
    this.time = new Date(event);
  }
    
}`;

  }

  ngOnInit() {
  }

  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }

  onChangeTime(event : any){
    this.time = new Date(event);
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }

}



