import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'pass-input',
  templateUrl: 'password.demo.html'
})
export class PasswordDemoComponent implements OnInit {
  tsCode:any;
  htmlCode:any;
  isExpanded : boolean=false;
  constructor() {
    this.htmlCode=`<!--Password Input-->
<amexio-password-input [fieldLabel]="'Password Input'" name ="password"
              [placeholder]="'Enter Password'"
              [allowBlank]="false" 
              [errorMsg] ="'Please enter password'"
              [minLength]="4" 
              [minErrorMsg]="'Minimum 4 char required'"
              [maxLength]="8"  
              [maxErrorMsg]="'Maximum 8 char allowed'"
              [iconFeedBack]="true"
</amexio-password-input>
     
<!--Password Input without label-->

<amexio-password-input name ="password"
              [placeholder]="'Enter Password'"
              [allowBlank]="false" 
              [errorMsg] ="'Please enter password'"
              [minLength]="4" 
              [minErrorMsg]="'Minimum 4 char required'"
              [maxLength]="8"  
              [maxErrorMsg]="'Maximum 8 char allowed'"
              [iconFeedBack]="true"
</amexio-password-input>
    `
    this.tsCode=`
import {Component, OnInit} from '@angular/core';
@Component({
  selector: 'pass-input',
  templateUrl: 'password.demo.html'
})
export class PasswordDemoComponent implements OnInit {
  constructor() {
  }
  ngOnInit() {
  }
}
    `
  }

  ngOnInit() {
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



