import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'checkbox',
  templateUrl: 'checkbox.demo.html'
})
export class CheckboxDemoComponent implements OnInit {
  checkboxGroupdata : any;
  htmlCode:any;
  tsCode : any;
  isExpanded : boolean=false;
  constructor() {
    this.checkboxGroupdata={
      response:{
        data:[{

          language:'Angular 2',
          languageId:'angular 2'
        },{
          language:'JAVA',
          languageId:'java'
        },{
          language:'C++',
          languageId:'C++'
        }
        ]}};
    this.htmlCode=`<!--Horizontal Checkbox-->
<amexio-checkbox [fieldLabel]="'Horizontal Checkbox'" name ="language"
                 [dataReader]="'response.data'"
                 [displayField]="'language'"
                 [valueField]="'languageId'"
                 [checkBoxGroupDownBindData]="checkboxGroupdata"
                 [column]="'4'">
</amexio-checkbox>
      
<!--Vertical Checkbox-->
<amexio-checkbox [fieldLabel]="'Vertical Checkbox'" name ="language"
                 [dataReader]="'response.data'"
                 [displayField]="'language'"
                 [valueField]="'languageId'"
                 [checkBoxGroupDownBindData]="checkboxGroupdata">
</amexio-checkbox>
    `
    this.tsCode=`
import {Component, OnInit} from '@angular/core';
@Component({
  selector: 'checkbox',
  templateUrl: 'checkbox.demo.html'
})
export class CheckboxDemoComponent implements OnInit {
  checkboxGroupdata : any;
  constructor() {
    this.checkboxGroupdata={
      response:{
        data:[{
          language:'Angular 2',
          languageId:'angular 2'
        },{
          language:'JAVA',
          languageId:'java'
        },{
          language:'C++',
          languageId:'C++'
        }
        ]}};
  }

  ngOnInit() {
  }
}

    `
  }

  ngOnInit() {
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



