import {ChangeDetectorRef, Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'dropdown-multiselect',
  templateUrl: 'dropdownmultiselect.demo.html'
})
export class DropDownMultiSelectComponent implements OnInit {
  tsCode:any;
  htmlCode:any;
  datasource:any;
  fruit :any;
  fruits :any;
  isExpanded:boolean=false;
  constructor(private ref: ChangeDetectorRef) {
    this.tsCode=`import {ChangeDetectorRef,Component, OnInit} from '@angular/core';

@Component({
  selector: 'dropdown-multiselect',
  templateUrl: 'dropdownmultiselect.demo.html'
})
export class DropDownMultiSelectComponent implements OnInit {
  fruit :any;
  fruits :any;
  constructor(private ref: ChangeDetectorRef) {
  }

  ngOnInit() {
  }
  setMultiVal(val : any){
    console.log(val);
    this.fruit = val.data;
    this.ref.detectChanges();
  }
   setMultiVal2(val : any){
    this.fruits = val.data;
    this.ref.detectChanges();
  }
}

    `
    this.htmlCode=`<!--MultiSelect-->
<amexio-dropdown  (onMultiSelect)="setMultiVal($event)"
         [placeholder]="'Choose'"
         [dataReader]="'response.data'"
         [fieldLabel]="'Pick Some Fruits'"
         [httpUrl]="'assets/data/dropdown/fruits.json'"
         [httpMethod]="'get'"
         [displayField]="'fruitName'"
         [valueField]="'code'"
         [width]="'auto'"
         [multiSelect]="'true'">
</amexio-dropdown>

<div class="panel-heading"><b>Selected Value</b></div>
<div class="panel-body">
    <div *ngFor="let ft of fruit">
      <p>{{ft.fruitName}}</p>
    </div>
</div>

<!--Multi Select With Limited Select-->
<amexio-dropdown  (onMultiSelect)="setMultiVal2($event)"
         [placeholder]="'Choose'"
         [dataReader]="'response.data'"
         [fieldLabel]="'Pick Two Fruits'"
         [httpUrl]="'assets/data/dropdown/fruits.json'"
         [httpMethod]="'get'"
         [displayField]="'fruitName'"
         [valueField]="'code'"
         [width]="'auto'"
         [multiSelect]="'true'"
         [maxMultiSelect]="'2'">
</amexio-dropdown>

<div class="panel-heading"><b>Only 2 Fruits</b></div>
      <div class="panel-body">
        <pre>
         {{fruits | json}}
        </pre>
      </div>
      
 <!--Count Display Mode-->     
<amexio-dropdown  [placeholder]="'Choose'"
         [dataReader]="'response.data'"
         [fieldLabel]="'Choose Option'"
         [httpUrl]="'assets/data/dropdown/fruits.json'"
         [httpMethod]="'get'"
         [displayField]="'fruitName'"
         [valueField]="'code'"
         [width]="'auto'"
         [multiSelect]="'true'"
         [multiCountDisplay]="'count'">
</amexio-dropdown>
    `
    this.datasource=`{
  "alarm" : null,
  "response" : {
    "success": true,
    "message": "Fetching  Data  Request Succeeded: Fruits",
    "data": [
      {"fruitName": "Apple","code": "Apple"},
      {"fruitName": "Oranges","code": "Oranges"},
      {"fruitName": "Watermelon","code": "Watermelon"},
      {"fruitName": "Avacado","code": "Avacado"},
      {"fruitName": "Grapes","code": "Grapes"},
      {"fruitName": "Mango","code": "Mango"},
      {"fruitName": "Banana","code": "Banana"}
    ]
  }
}

    `
  }

  ngOnInit() {
  }
  setMultiVal(val : any){
    console.log(val);
    this.fruit = val.data;
    this.ref.detectChanges();
  }
  setMultiVal2(val : any){
    this.fruits = val.data;
    this.ref.detectChanges();
  }

  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



