import { Component, OnInit} from '@angular/core';
declare var $;
@Component({
  moduleId: module.id,
  selector: 'progress-demo',
  templateUrl: 'progress.demo.html'
})

export class ProgressDemoComponent implements OnInit{

  htmlCode : string;
  tsCode : string;
  buttonLabel : string = 'Stop';
  buttonState : boolean = false;
  progressStat : boolean = true;
  isExpanded : boolean=false;
  constructor(){
    this.htmlCode = `<!--Simple Progress bar-->
<progress-bar 
      [showProgress]="progressStat">
</progress-bar>
<button-action [label]="buttonLabel" 
      [type]="'primary'" 
      [tooltipMessage]="buttonLabel" 
      [disabled]="buttonState" 
      (onClick)="buttonClick($event)">
</button-action>
     
    `;
    this.tsCode = `import { Component, OnInit} from '@angular/core';
@Component({
  selector: 'progress-demo',
  templateUrl: 'progress.demo.html'
})

export class ProgressDemoComponent implements OnInit{
  buttonLabel : string = 'Stop';
  progressStat : boolean = true;
  ngOnInit(){}
  buttonClick(event : any){
      if(this.progressStat){
        this.progressStat = !this.progressStat;
        this.buttonLabel = 'Start';
      }
      else{
        this.progressStat = !this.progressStat;
        this.buttonLabel = 'Stop';
      }
    }
}
    `;
  }

  ngOnInit(){

  }


  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }

  buttonClick(event : any){
    if(this.progressStat){
      this.progressStat = !this.progressStat;
      this.buttonLabel = 'Start';
    }
    else{
      this.progressStat = !this.progressStat;
      this.buttonLabel = 'Stop';
    }
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}




