import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'selector',
  templateUrl: 'dropdownbasic.demo.html'
})
export class DropDownBasicComponent implements OnInit {
  tsCode: any;
  htmlCode : any;
  sportNameObj : any;
  sportName : string;
  isExpanded:boolean=false;
  datasource: any;
  constructor() {
    this.htmlCode=`<!--Basic Dropdown-->
<amexio-dropdown [(ngModel)]="sportName"
             [placeholder]="'Choose'"
             name="sportName"
             [dataReader]="'response.data'"
             [fieldLabel]="'Choose Sport'"
             [httpUrl]="'assets/data/dropdown/sports.json'"
             [httpMethod]="'get'"
             [displayField]="'sportName'"
             [valueField]="'code'"
             [width]="'250px'"
             (onSingleSelect)="getSingleVal($event)">
</amexio-dropdown>

<div class="well well-sm">Selected Value: <b>{{sportName}}</b></div>


<div> <pre>{{sportNameObj | json }}</pre></div>
    `

    this.tsCode=`import {Component, OnInit} from '@angular/core';
@Component({
  selector: 'selector',
  templateUrl: 'dropdownbasic.demo.html'
})
export class DropDownBasicComponent implements OnInit {
  sportName : string;
   sportNameObj : any;
  constructor() {
  }
  ngOnInit() {
  }
  getSingleVal(value : any){
    this.sportNameObj = value.data;
  }
}
         
    `
    this.datasource=`{
  "alarm" : null,
  "response" : {
    "success": true,
    "message": "Fetching  Data  Request Succeeded: Sport",
    "data": [
      {"sportName": "Cricket","code": "Ck"},
      {"sportName": "Swimming","code": "Sw"},
      {"sportName": "Golf","code": "Glf"},
      {"sportName": "Hockey","code": "Hky"},
      {"sportName": "Soccer","code": "Scr"},
      {"sportName": "Formula One","code": "F1"}
    ]
  }
}

    
    `
  }

  ngOnInit() {
  }
  getSingleVal(value : any){
    this.sportNameObj = value.data;
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



