
import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'datagrid-template-demo',
  templateUrl: 'datagrid.template.html'
})
export class DataGridTemplateComponent implements OnInit {

  htmlCode : string;
  tsCode : string;
  datasource : string;
  data : any[];
  clickedRowData : any;
  isExpanded : boolean=false;


  constructor() {
    this.htmlCode = `
<amexio-data-table
                    [title]="'Data Grid'"
                    [pageSize] = "10"
                    [dataReader]="'response.data'"
                    [checkboxSelect]="true"
                    [httpUrl]="'assets/data/countries.json'"
                    [httpMethod]="'get'"
                    (selectedRowData)="onRowSelect($event)"
                    (rowSelect)="onRowClick($event)">
                    <amexio-data-table-column [dataIndex]="'name_official'" [dataType]="'string'" [hidden]="false" [text]="'Name'">
                      <ng-template #amexioBodyTmpl let-column let-row="row">
                        <span>
                            <img src="" [attr.src]="'assets/images/flags/flags/'+row.flag_32">&nbsp;{{row.name}} ( {{row.name_official}} )
                        </span>
                      </ng-template>
                    </amexio-data-table-column>
                    <amexio-data-table-column [dataIndex]="'latitude'" [dataType]="'string'" [hidden]="false" [text]="'Latitude'"></amexio-data-table-column>
                    <amexio-data-table-column [dataIndex]="'longitude'" [dataType]="'string'" [hidden]="false" [text]="'Longitude'"></amexio-data-table-column>
                  </amexio-data-table>`;


    this.tsCode = `import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'datagrid-template-demo',
  templateUrl: 'datagrid.template.html'
})
export class DataGridTemplateComponent implements OnInit {
    constructor(){}
    ngOnInit(){}

}
    `;

    this.datasource = `{
  "response": {
    "success": true,
    "message": "Fetching  Data  Request Succeeded: Country",
    "data": [
      {
        "id": 1,
        "enabled": "1",
        "code3l": "AFG",
        "code2l": "AF",
        "name": "Afghanistan",
        "name_official": "Islamic Republic of Afghanistan",
        "flag_32": "AF-32.png",
        "flag_128": "AF-128.png",
        "latitude": "33.98299275",
        "longitude": "66.39159363",
        "zoom": "6"
      },
      {
        "id": 199,
        "enabled": "1",
        "code3l": "ALA",
        "code2l": "AX",
        "name": "land Islands",
        "name_official": "land Islands",
        "flag_32": "AX-32.png",
        "flag_128": "AX-128.png",
        "latitude": "60.25403213",
        "longitude": "20.35918350",
        "zoom": "9"
      },
      {
        "id": 2,
        "enabled": "1",
        "code3l": "ALB",
        "code2l": "AL",
        "name": "Albania",
        "name_official": "Republic of Albania",
        "flag_32": "AL-32.png",
        "flag_128": "AL-128.png",
        "latitude": "41.00017358",
        "longitude": "19.87170014",
        "zoom": "7"
      },
      {
        "id": 3,
        "enabled": "1",
        "code3l": "DZA",
        "code2l": "DZ",
        "name": "Algeria",
        "name_official": "Peoples Democratic Republic of Algeria",
        "flag_32": "DZ-32.png",
        "flag_128": "DZ-128.png",
        "latitude": "27.89861690",
        "longitude": "3.19771194",
        "zoom": "5"
      },
      {
        "id": 197,
        "enabled": "1",
        "code3l": "ASM",
        "code2l": "AS",
        "name": "American Samoa",
        "name_official": "The United States Territory of American Samoa",
        "flag_32": "AS-32.png",
        "flag_128": "AS-128.png",
        "latitude": "-14.30634641",
        "longitude": "-170.69501750",
        "zoom": "11"
      }
     ]
    }
  }
`;


  }

  ngOnInit() {
  }

  onRowSelect(data : any){
    this.data = data;
  }

  onRowClick(data : any){
    this.clickedRowData = data;
  }

  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }

  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }

}



