import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'CarouselView-demo',
  templateUrl : 'carouselview.demo.html'
})
export class CarouselViewDemoComponent implements OnInit {

  htmlCode : string;
  tsCode : string;
  datasource : string;
  isExpanded :boolean=false;

  constructor() {
    this.htmlCode = `<amexio-carousel  [dataReader]="'data'" 
           [httpMethod]="'get'" 
           [httpUrl]="'assets/data/scrollview.json'">
</amexio-carousel>
    
    `;

    this.tsCode = `import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'CarouselView-demo',
  templateUrl : 'Carouselview.demo.html'
})
export class CarouselViewDemoComponent implements OnInit {
  constructor() {}
  ngOnInit() {}
}
    `;
    this.datasource =`
    {
    "data": [
      {
        "title":"view 1",
        "img":"./assets/images/1.jpg",
        "caption":"Chicago",
        "content":"<div style='width: 100%;height: 200px; background-color: gray'><p>If you chose to do a Trial Project, then FBE customer engineers will request data about your precast pieces to put into MH Pro! so that the Shop and Submittal Drawings have the content and format your company requires. There are different versions of the Shop Drawing available in the English DemoBuild for any structure in the sample jobs.  You may chose elements from these Shops Drawings to define what your Shop Drawing will look like.</p></div>",
        "active" : true
      },
      {
        "title":"view 2",
        "img":"./assets/images/2.jpg",
        "content":"<div style='width: 100%;height: 200px; background-color: gray'><p>Eco Nature Wordpress Theme is created to help you make worlds environment cleaner and save the nature. Unlimited design solutions can be created due to a great flexibility of settings and shortcode options. Eco Nature theme has a Portfolio that can be set to any sort of a grid or a Masonry puzzle with variable paddings for elements, while Blog can have several different layouts too. Custom Profiles and Projects post types offer an optimal way to present your ecology content.</p></div>",
        "caption":"Chicago",
        "active" : false
      },
      {
        "title":"view 3",
        "img":"./assets/images/4.jpg",
        "content":"<div style='width: 100%;height: 200px; background-color: gray'><p>You can create any sort of Environment website, like a website for a ecology preserving movement, Green Earth organization, any sort of organic life project, animals saving fundraiser. Eco Nature comes with a Custom Mega Menu plugin, a really functional Form Builder and premium Revolution and Layer Slider plugins included for free. Multilanguage, RTL-ready, plugin-compatible and professionally-supported theme will make every day you work with your website be a great experience! With a special Donations functionality support, you can collect paypal donations for your ecology project, to save nature, animals and organic life.</p></div>",
        "caption":"Los Angeles",
        "active" : false
      },
      {
        "title":"view 4",
        "img":"./assets/images/5.jpg",
        "content":"<div style='width: 100%;height: 200px; background-color: gray'><p>Teachers not only demonstrate specific learning concepts within the classroom, they can also participate in demonstration classrooms to help improve their own teaching strategies, which may or may not be demonstrative in nature. Although the literature is limited, studies show that the effects of demonstration classroom teachers includes a change of perspective in relating to students, more reflection in the teachers own classroom strategies, and more personal responsibility for student learning</p></div>",
        "caption":"New York",
        "active" : false
      }
    ]
  }


    `
  }

  ngOnInit() {
  }
  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }

  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



