import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  moduleId: module.id,
  selector: 'button-showcase',
  templateUrl : 'button.demo.html'
})
export class ButtonDemoComponent implements OnInit {

  asyncFlag : boolean;
  htmlCode : string;
  tsCode : string;
  isExpanded :boolean=false;

  constructor() {
    this.htmlCode = `<!--Button Types-->
<amexio-btn [label]="'Default'" 
            [type]="'default'" 
            [tooltipMessage]="'Default Button'">
</amexio-btn>
                       
<amexio-btn [label]="'Primary'" 
            [type]="'primary'" 
            [tooltipMessage]="'Primary Button'">
</amexio-btn>
                     
<amexio-btn [label]="'Success'" 
            [type]="'success'" 
            [tooltipMessage]="'Success Button'">
</amexio-btn>
                        
<amexio-btn [label]="'Danger'" 
            [type]="'danger'" 
            [tooltipMessage]="'danger Button'">
</amexio-btn>
                      
<amexio-btn [label]="'Warning'" 
            [type]="'warning'" 
            [tooltipMessage]="'Warning Button'">
</amexio-btn>

<!--Button States-->

<amexio-btn [label]="'Disabled'" 
            [type]="'primary'" 
            [tooltipMessage]="'Disabled'" 
            [disabled]="'true'" >
</amexio-btn>
                      
<amexio-btn [label]="'Enabled'" 
            [type]="'primary'" 
            [tooltipMessage]="'Enabled'" >
</amexio-btn>
                  
<amexio-btn [label]="'Async Task'" 
            [type]="'primary'" 
            [tooltipMessage]="'Async Task'" 
            (onClick)="switchasyncFlag()" 
            [isLoading]="asyncFlag">
</amexio-btn>

<!--Button with Icons-->

<amexio-btn [label]="'Next'" 
            [type]="'primary'" 
            [tooltipMessage]="'Next'" 
            icon="chevron-right">
</amexio-btn>
                   
<amexio-btn [label]="'Download'" 
            [type]="'success'" 
            [tooltipMessage]="'Save'" 
            icon="save"></amexio-btn>
                      
<amexio-btn [label]="'Delete'" 
            [type]="'danger'" 
            [tooltipMessage]="'delete'" 
            icon="trash">
</amexio-btn>


<!--Button Sizes-->

<amexio-btn [label]="'Default Button'" 
            [type]="'primary'" 
            [tooltipMessage]="'default'">
</amexio-btn>
                        
<amexio-btn [label]="'Small Button'" 
            [type]="'primary'" 
            [tooltipMessage]="'small'" 
            [size]="'small'">
</amexio-btn>
                         
<amexio-btn [label]="'X-Small Button'" 
            [type]="'primary'" 
            [tooltipMessage]="'x - small'" 
            [size]="'xsmall'">
</amexio-btn>

<!--Button Block-->

<amexio-btn [label]="'Block Button'" 
            [type]="'default'" 
            [tooltipMessage]="'default'" 
            [size]="'large'" 
            [block]="'true'">
</amexio-btn>
                        
<amexio-btn [label]="'Block Button'" 
            [type]="'success'" 
            [tooltipMessage]="'default'" 
            [size]="'large'" 
            [block]="'true'">
</amexio-btn>
                        
<amexio-btn [label]="'Block Button'" 
            [type]="'warning'" 
            [tooltipMessage]="'default'" 
            [size]="'large'" 
            [block]="'true'">
</amexio-btn>
                         
                          
                       
                      
`;
    this.tsCode = `import {Component, OnInit} from '@angular/core';

@Component({
  moduleId: module.id,
  selector: 'button-showcase',
  templateUrl : 'button.demo.html'
})
export class ButtonDemoComponent implements OnInit {

  asyncFlag : boolean;
  constructor();
  ngOnInit();
  
  //Replace this with your HTTP calls and set boolean in () method.
  switchasyncFlag(){
   this.asyncFlag = true;
    setTimeout(() => {
      this.asyncFlag = false    //For demo purpose.
    }, 3000);
  }`;
  }

  ngOnInit() {
  }

  switchasyncFlag(){
   this.asyncFlag = true;
    setTimeout(() => {
      this.asyncFlag = false
    }, 3000);
  }

  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }

  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



