import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'text-input',
  templateUrl: 'textinput.demo.html'
})
export class TextInputDemoComponent implements OnInit {
  tsCode: any;
  htmlCode:any;
  isExpanded :boolean=false;
  constructor() {
    this.tsCode=`
import {Component, OnInit} from '@angular/core';
@Component({
  selector: 'text-input',
  templateUrl: 'textinput.demo.html'
})
export class TextInputDemoComponent implements OnInit {
  constructor() {
  }
  ngOnInit() {
  }
}

    `
    this.htmlCode=`<!--Simple Text Input-->
<amexio-text-input [fieldLabel]="'Name'" name ="name"
                   [placeholder]="'Enter name'"
                   [iconFeedBack]="true"
</amexio-text-input>

<!--Text Input Without Label and Icon feedback-->

<amexio-text-input name ="name"
                   [placeholder]="'Enter name'"
                   [iconFeedBack]="false"
                   [hasLabel]="false">
</amexio-text-input>

<!--Text Input with Validations-->

<amexio-text-input  [fieldLabel]="'Name'" name ="name"
                    [placeholder]="'Enter name'"
                    [allowBlank]="false" [errorMsg] ="'Please enter last name'"
                    [minLength]="1" [minErrorMsg]="'Minimum 1 char required'"
                    [maxLength]="10"  [maxErrorMsg]="'Maximum 10 char allowed'"
                    [iconFeedBack]="true">
</amexio-text-input>

<!--Text Input disabled Property-->

<amexio-text-input name ="name" 
                   [fieldLabel]="'Name'"
                   [placeholder]="'Enter name'"
                   [disabled]="true">
</amexio-text-input>

<!--Text Input with Font Style : italic-->

<amexio-text-input name ="name" 
                   [fieldLabel]="'Name'"
                   [placeholder]="'Enter name'"
                   [fontStyle]="'italic'">
</amexio-text-input>

<!--Text Input with Font Family : Times New Roman and Font Size : large-->

<amexio-text-input name ="name" 
                   [fieldLabel]="'Name'"
                   [placeholder]="'Enter name'"
                   [fontFamily]="'Times New Roman'"
                   [fontSize]="'large'">
</amexio-text-input>
    `
  }

  ngOnInit() {
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



