export class SideNavComponent implements OnInit,AfterViewInit {

  searchInputRef : any;


  constructor(private router : Router) {

  }

  ngOnInit() {
  }

  ngAfterViewInit(){
    $("#side-menu").metisMenu();
    this.searchInputRef = document.getElementById('appsearchbox');
// Show label but insert value into the input:
    new Awesomplete(this.searchInputRef, {
      list: [
        { label: "TextField", value: "inputs" },
        { label: "Input", value: "inputs" },
        { label: "Forms", value: "forms" },
        { label: "Buttons", value: "buttons" },
        { label: "Rating", value: "rating" },
        { label: "Datagrid", value: "datagrid" },
        { label: "Tree", value: "treeview" },
        { label: "Dropdown", value: "dropdown" },
        { label: "Datetime", value: "datetime" },
        { label: "Tabs", value: "tabs" },
        { label: "TreeGrid", value: "treegrid" },
        { label: "Notification", value: "notify" },
        { label: "ItemSelector" , value : "itemselector"},
        { label : "ScrollView" , value : "scrollview"},
        { label : "Progress" , value : "progress"}
      ]
    });
  }

  searchSelect(event : any,elementRef : any){
    if(event.text != null){
      let route : string= event.text.value;
      this.router.navigate([route]);
    }
  }


}




