import {AfterViewInit, Component, OnInit} from '@angular/core';

declare var $;

@Component({
  selector: 'form-fields',
  templateUrl: 'form.fields.html'
})
export class FormFieldsComponent implements OnInit,AfterViewInit {
  radioGroupData : any;
  checkboxGroupdata : any;
  htmlCode : string;
  tsCode : string;
  constructor() {
    this.radioGroupData={
            response:{
              data:[{
                gender:'Male',
                genderId:'male'
              },{
                gender:'Female',
                genderId:'female'
              },
              ]}}

    this.checkboxGroupdata={
      response:{
        data:[{

          language:'Angular 2',
          languageId:'angular 2'
        },{
          language:'JAVA',
          languageId:'java'
        },{
          language:'C++',
          languageId:'C++'
        }
        ]}};

    this.htmlCode = `
    
    <form role="form">
                      <div class="row">
                        <div class="col-lg-6">
                          <text-input [fieldLabel]="'Text Input'" name ="lastname"
                                      [placeholder]="'Enter name'"
                                      [allowBlank]="false" [errorMsg] ="'Please enter last name'"
                                      [minLength]="1" [minErrorMsg]="'Minimum 1 char required'"
                                      [maxLength]="10"  [maxErrorMsg]="'Maximum 10 char allowed'"
                                      [iconFeedBack]="true"
                          ></text-input>
                        </div>
                        <div class="col-lg-6">
                          <input-number [fieldLabel]="'Number Input'" name ="age"
                                        [placeholder]="'Enter Age'"
                                        [allowBlank]="false" [errorMsg] ="'Please enter phone no'"
                                        [maxValue]="100"  [maxErrorMsg]="'Maximum 10 digit allowed'"
                                        [iconFeedBack]="true"
                          ></input-number>
                        </div>
                      </div>
                      <div class="row">
                        <div class="col-lg-6">
                          <email-input [fieldLabel]="'Email Input'" name ="Email"
                                       [placeholder]="'Enter Email id'"
                                       [allowBlank]="false" [errorMsg] ="'Please enter Email Id'"
                                       [iconFeedBack]="true"
                          ></email-input>
                        </div>
                        <div class="col-lg-6">
                          <password-input [fieldLabel]="'Password Input'" name ="password"
                                          [placeholder]="'Enter Password'"
                                          [allowBlank]="false" [errorMsg] ="'Please enter password'"
                                          [minLength]="4" [minErrorMsg]="'Minimum 4 char required'"
                                          [maxLength]="8"  [maxErrorMsg]="'Maximum 8 char allowed'"
                                          [iconFeedBack]="true"
                          ></password-input>
                        </div>
                      </div>
                      <div class="row">
                        <div class="col-lg-12">
                          <text-area-input [fieldLabel]="'Textarea Input'" name ="Address"
                                           [placeholder]="'permanent Address'"
                                           [allowBlank]="true" [errorMsg] ="'Please enter Address'"
                                           [iconFeedBack]="true" [noOfrows]="'4'" [noOfCols]="'2'"
                          ></text-area-input>
                        </div>
                      </div>
                      <div class="row">
                        <div class="col-lg-6">
                          <radio-group [fieldLabel]="'RadioGroup Input'" name ="gender"
                                       [fieldName]="'gender'"
                                       [dataReader]="'response.data'"
                                       [displayField]="'gender'"
                                       [valueField]="'genderId'"
                                       [radioGroupBindData]="radioGroupData"
                                       (selectedValue)="setSelectedGender($event)"
                                       [column]="'6'"
                          ></radio-group>
                        </div>
                        <div class="col-lg-6">
                          <check-group [fieldLabel]="'Checkbox Input'" name ="language"
                                       [dataReader]="'response.data'"
                                       [displayField]="'language'"
                                       [valueField]="'languageId'"
                                       [checkBoxGroupDownBindData]="checkboxGroupdata"
                                       [column]="'4'"
                          ></check-group>
                        </div>
                      </div>
                    </form>
   
    
    `;



    this.tsCode=`
    
  import {AfterViewInit, Component, OnInit} from '@angular/core';
  @Component({
    selector: 'form-fields',
    templateUrl: 'form.fields.html'
  })
  export class FormFieldsComponent implements OnInit,AfterViewInit {
    radioGroupData : any;
    checkboxGroupdata : any;
    constructor() {
      this.radioGroupData={
              response:{
                data:[{
                  gender:'Male',
                  genderId:'male'
                },{
                  gender:'Female',
                  genderId:'female'
                }
                ]}}
      this.checkboxGroupdata={
              response:{
                data:[{
                  language:'C++',
                  languageId:'C++'
                },{
                  language:'JAVA',
                  languageId:'java'
                },{
                  language:'Angular 2',
                  languageId:'angular 2'
                }
                ]}}
    }
    ngOnInit() {
    }
  }

    
    `
  }

  ngOnInit() {
  }

  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }

  ngAfterViewInit(){
    $('[dataviews-submenu]').submenupicker();
    $('[dataviews-toggle="popover"]').popover();
  }
  setSelectedGender(data : any){

  }
}



