import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'tree-view-demo',
  templateUrl: 'treeview.demo.html'
})
export class TreeViewDemoComponent implements OnInit {

  htmlCode : string;
  tsCode : string;
  datasource : string;
  data : any;
  isExpanded : boolean=false;
  constructor() {
    this.htmlCode = `<amexio-tree-view
        [dataReader]="'data'" [httpMethod]="'get'"
        [httpUrl]="'assets/data/treeview.json'"
        (selectedRecord)="onRowSelect($event)">
</amexio-tree-view>
    `;

    this.tsCode = `import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'tree-view-demo',
  templateUrl: 'treeview.demo.html'
})
export class TreeViewDemoComponent implements OnInit {
  constructor(){}
  ngOnInit(){}

}`;

    this.datasource = `{
  "data": [{
    "text": "Web App",
    "expanded": true,
    "children": [
      {
        "text": "app",
        "expanded": true,
        "children": [
          {
            "leaf": true,
            "text": "Application.js"
          }
        ]
      },
      {
        "text": "button",
        "expanded": true,
        "children": [
          {
            "leaf": true,
            "text": "Button.js"
          },
          {
            "leaf": true,
            "text": "Cycle.js"
          },
          {
            "leaf": true,
            "text": "Split.js"
          }
        ]
      },
      {
        "text": "container",
        "expanded": true,
        "children": [
          {
            "leaf": true,
            "text": "ButtonGroup.js"
          },
          {
            "leaf": true,
            "text": "Container.js"
          },
          {
            "leaf": true,
            "text": "Viewport.js",
            "expanded" : false,
            "children":[],
            "lazy":{
              "httpUrl":"data/treeview.json",
              "httpMethod": "get"
            }
          }
        ]
      },
      {
        "text": "core",
        "expanded": true,
        "children": [
          {
            "text": "dom",
            "expanded": false,
            "children": [
              {
                "leaf": true,
                "text": "Element.form.js"
              },
              {
                "leaf": true,
                "text": "Element.static-more.js"
              }
            ]
          }
        ]
      }
    ]
  }]
}`;
  }

  ngOnInit() {
  }

  onRowSelect(data : any){
    this.data = data;
  }

  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



