import {Component, OnInit} from '@angular/core';
import {WidgetService} from "amexio-angular-extensions";
declare var $;
@Component({
  moduleId: module.id,
  selector: 'notify-demo',
  templateUrl: 'notification.demo.html',
  providers : [WidgetService]
})
export class NotifiyDemoComponent implements OnInit {

  htmlCode : string;
  tsCode : string;
  isExpanded : boolean=false;

  constructor(public widgetService : WidgetService) {
    this.htmlCode = `<!--Primary Info message button-->
<amexio-btn 
    (onClick)="widgetService.toasterInfo('Demo Info','Sample Info Message')" 
    [label]="'Info Popup'" [type]="'primary'" 
    [tooltipMessage]="'Primary Button'">
</amexio-btn>
   <!--Success Info message button-->   
<amexio-btn 
    (onClick)="widgetService.toasterSuccess('Demo Success','Sample Success Message')" 
    [label]="'Success Popup'" [type]="'success'" 
    [tooltipMessage]="'Success Button'">
</amexio-btn>
    <!--Danger Info message button-->       
<amexio-btn 
    (onClick)="widgetService.toasterDanger('Demo Danger','Sample Danger Message')" 
    [label]="'Danger Popup'" [type]="'danger'" 
    [tooltipMessage]="'danger Button'">
</amexio-btn>
`;
    this.tsCode = `import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'notify-demo',
  templateUrl: 'notification.demo.html'
})
export class NotifiyDemoComponent implements OnInit {
  constructor(private widgetService : WidgetService) {}
  ngOnInit() {}
  }
  `;
  }

  ngOnInit() {
  }

  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }

  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}




