import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  moduleId: module.id,
  selector: 'textarea-input',
  templateUrl: 'textarea.demo.html'
})
export class TextareaDemoComponent implements OnInit {
  tsCode: any;
  htmlCode:any;
  isExpanded :boolean=false;
  constructor() {
    this.tsCode=`
import {Component, OnInit} from '@angular/core';
@Component({
  selector: 'textarea-input',
  templateUrl: 'textarea.demo.html'
})
export class TextareaDemoComponent implements OnInit {
  constructor() {
  }
  ngOnInit() {
  }
}
    `
    this.htmlCode=`<!--TextArea Input-->

<amexio-textarea-input [fieldLabel]="'Textarea Input'" name ="Address"
             [placeholder]="'permanent Address'"
             [allowBlank]="true" [errorMsg] ="'Please enter Address'"
             [iconFeedBack]="true" [noOfrows]="'4'" [noOfCols]="'2'">
</amexio-textarea-input>

<!--TextArea Input with rows 7 and columns 4-->

<amexio-textarea-input [fieldLabel]="'Textarea Input'" name ="Address"
             [placeholder]="'permanent Address'"
             [allowBlank]="true" [errorMsg] ="'Please enter Address'"
             [iconFeedBack]="true" [noOfrows]="'7'" [noOfCols]="'4'">
</amexio-textarea-input>
    `
  }

  ngOnInit() {
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



