import {AfterContentChecked, Component, OnInit, QueryList, ViewChild, ViewChildren} from '@angular/core';
import {WidgetService,FormInputBase} from "amexio-angular-extensions"

declare var $;
@Component({
  moduleId: module.id,
  selector: 'input-form',
  templateUrl: 'input.forms.html',
  providers :[WidgetService]

})
export class InputFormComponent implements OnInit,AfterContentChecked {

  ngAfterContentChecked(): void {
    $('.selectpicker').selectpicker('refresh');
  }
  @ViewChildren(FormInputBase)
  private formInputBase : QueryList<any>;
  radioGroupData : any;
  htmlCode : string;
  tsCode : string;
  inputFormModel : InputFormModel;
  constructor(private widgetService : WidgetService) {
    this.inputFormModel = new InputFormModel();
    this.radioGroupData={
      response:{
        data:[{
          gender:'Male',
          genderId:'male'
        },{
          gender:'Female',
          genderId:'female'
        },
        ]}};

        this.htmlCode=`
        <div class="row">
    <div class="tab-content">
      <div id="view1" class="tab-pane fade in active">

        <div class="row">
          <div class="col-lg-12">
            <div class="panel panel-default">
              <div class="panel-heading">
                Contact Details Form
              </div>
              <div class="panel-body">
                <div class="row">
                  <div class="col-md-6 col-md-offset-3">
                    <form role="form">
                      <div class="row">
                        <div class="col-lg-12">
                          <text-input [fieldLabel]="'FirstName'" name ="firstName"
                                      [(ngModel)]="inputFormModel.firstName" [placeholder]="'First Name'"
                                      [allowBlank]="false" [errorMsg] ="'Please enter first name'"
                                      [minLength]="1" [minErrorMsg]="'Minimum 1 char required'"
                                      [maxLength]="20"  [maxErrorMsg]="'Maximum 20 char allowed'"
                                      [iconFeedBack]="true"
                          ></text-input>
                        </div>
                      </div>
                      <div class="row">
                        <div class="col-lg-12">
                          <text-input [fieldLabel]="'LastName'" name ="lastName"
                                      [(ngModel)]="inputFormModel.lastName" [placeholder]="'Last Name'"
                                      [allowBlank]="false" [errorMsg] ="'Please enter last name'"
                                      [minLength]="1" [minErrorMsg]="'Minimum 1 char required'"
                                      [maxLength]="10"  [maxErrorMsg]="'Maximum 10 char allowed'"
                                      [iconFeedBack]="true"
                          ></text-input>
                        </div>

                      </div>


                      <email-input [fieldLabel]="'E-mail'" name ="emailId"
                                   [(ngModel)]="inputFormModel.emailId" [placeholder]="'E-mail Address'"
                                   [allowBlank]="false" [errorMsg] ="'Please enter Email Id'"
                                   [iconFeedBack]="true"
                      ></email-input>
                      <text-input [fieldLabel]="'Phone #'" name ="phoneNo"
                                  [(ngModel)]="inputFormModel.phoneNo" [placeholder]="'8625895887'"
                                  [allowBlank]="false" [errorMsg] ="'Please enter Phone No'"
                                  [minLength]="10" [minErrorMsg]="'Minimum 10 char required'"
                                  [maxLength]="10"  [maxErrorMsg]="'Maximum 10 char allowed'"
                                  [iconFeedBack]="true"
                      ></text-input>

                      <radio-group [fieldLabel]="'Gender'" name ="gender"
                                   [fieldName]="'gender'"
                                   [dataReader]="'response.data'"
                                   [displayField]="'gender'"
                                   [valueField]="'genderId'"
                                   [radioGroupBindData]="radioGroupData"
                                   (selectedValue)="setSelectedGender($event)"
                                   [column]="'6'"
                      ></radio-group>
                      <input-number [fieldLabel]="'Age'" name ="age"
                                    [(ngModel)]="inputFormModel.age" [placeholder]="'Age'"
                                    [allowBlank]="false" [errorMsg] ="'Please enter age'"
                                    [maxValue]="100"  [maxErrorMsg]="'Maximum 3 digit allowed'"
                                    [iconFeedBack]="true"
                      ></input-number>
                      <div class="row">
                        <div class="col-lg-12">
                          <text-area-input [fieldLabel]="'Address'" name ="address"
                                           [(ngModel)]="inputFormModel.address" [placeholder]="'Address'"
                                           [allowBlank]="true" [errorMsg] ="'Please enter Address'"
                                           [iconFeedBack]="true" [noOfrows]="'4'" [noOfCols]="'2'"
                          ></text-area-input>
                        </div>
                      </div>
                      <div class="row">
                        <div class="col-lg-12">
                          <div class="form-group">
                            <drop-down [(ngModel)]="inputFormModel.country"
                                       [placeholder]="'Choose'"
                                       name="country"
                                       [dataReader]="'response.data'"
                                       [fieldLabel]="'Country'"
                                       [httpUrl]="'assets/data/country.json'"
                                       [httpMethod]="'get'"
                                       [searchBox]="'true'"
                                       [displayField]="'countryName'"
                                       [valueField]="'countryName'"
                            >
                            </drop-down>
                          </div>
                        </div>
                      </div>
                      <div class="row">
                        <div class="col-lg-12">
                          <drop-down [(ngModel)]="inputFormModel.state"
                                     [placeholder]="'Choose state'"
                                     name="state"
                                     [dataReader]="'response.data'"
                                     [fieldLabel]="'State'"
                                     [httpUrl]="'assets/data/country.json'"
                                     [httpMethod]="'get'"
                                     [searchBox]="'true'"
                                     [displayField]="'countryName'"
                                     [valueField]="'countryName'"
                          >
                          </drop-down>
                        </div>
                      </div>


                      <div class="row">
                        <div class="col-lg-12">
                          <drop-down [(ngModel)]="inputFormModel.city"
                                     [placeholder]="'Choose city'"
                                     name="city"
                                     [dataReader]="'response.data'"
                                     [fieldLabel]="'City'"
                                     [httpUrl]="'assets/data/country.json'"
                                     [httpMethod]="'get'"
                                     [searchBox]="'true'"
                                     [displayField]="'countryName'"
                                     [valueField]="'countryName'"
                          >
                          </drop-down>
                        </div>
                      </div>

                      <br><br>
                      <div class="col-md-6 col-md-offset-6">
                        <div class="col-lg-6">
                          <button-action [label]="'Reset'" [type]="'primary'" [tooltipMessage]="'Reset'" (onClick)="onResetClick($event)"></button-action>
                        </div>
                        <div class="col-lg-6">
                          <button-action [label]="'Save'" [type]="'success'" [tooltipMessage]="'Save'" (onClick)="onSaveClick($event)"></button-action>
                        </div>
                      </div>


                    </form>
                  </div>
                  <!-- /.col-lg-6 (nested) -->

                  <!-- /.col-lg-6 (nested) -->
                </div>
                <!-- /.row (nested) -->
              </div>
              <!-- /.panel-body -->
            </div>
            <!-- /.panel -->
          </div>
          <!-- /.col-lg-12 -->
        </div>
        <div class="row">

          <div class="well well-lg">
            <pre>
              {{inputFormModel |json}}
            </pre>

          </div>

        </div>




      </div>
      <div id="menu1" class="tab-pane fade">





      </div>

    </div>
  </div>
        
        `;

    this.tsCode=`
   

import {AfterContentChecked, Component, OnInit, QueryList, ViewChild, ViewChildren} from '@angular/core';
import {WidgetService} from "../../../components/bootstrap/widget.shared.service";
import {FormInputBase} from "../../../components/bootstrap/baseclass/form.input.base";
declare var $;
@Component({
  moduleId: module.id,
  selector: 'input-form',
  templateUrl: 'input.forms.html'
})
export class InputFormComponent implements OnInit,AfterContentChecked {

  ngAfterContentChecked(): void {
    $('.selectpicker').selectpicker('refresh'); // TODO : Check Performance
  }
   inputFormModel : InputFormModel;
  constructor(private widgetService : WidgetService) {
    this.inputFormModel = new InputFormModel();
    this.radioGroupData={
      response:{
        data:[{
          gender:'Male',
          genderId:'male'
        },{
          gender:'Female',
          genderId:'female'
        },
        ]}};
  }
  ngOnInit() {
  }
  
    setSelectedGender(data : any){
    this.inputFormModel.gender = data[0].gender;
  }

  onResetClick(data : any){
    this.inputFormModel = new InputFormModel();
  }
  onSaveClick(data : any){
    console.log(this.formInputBase.toArray());
    this.widgetService.validateAll(this.formInputBase.toArray());
    

  }
}

export class InputFormModel{
  firstName : string;
  lastName : string;
  phoneNo : number;
  emailId : string;
  age : number;
  address : string;
  country : string;
  state : string;
  city : string;
  gender : string;

}

    `;

  }
  ngOnInit() {
  }

  setSelectedGender(data : any){
    this.inputFormModel.gender = data[0].gender;
  }

  onResetClick(data : any){
    this.inputFormModel = new InputFormModel();
  }
  onSaveClick(data : any){
    this.widgetService.validateAll(this.formInputBase.toArray());

  }
}


export class InputFormModel{
  firstName : string;
  lastName : string;
  phoneNo : number;
  emailId : string;
  age : number;
  address : string;
  country : string;
  state : string;
  city : string;
  gender : string;

}



