import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'buttongroup',
  templateUrl: 'buttongroup.demo.html'
})
export class ButtonGroupDemoComponent implements OnInit {
  asyncFlag : boolean;
  htmlCode : string;
  tsCode : string;
  isExpanded :boolean=false;
  constructor() {

    this.htmlCode=`<!--Button Group Types-->
<amexio-btn-group>
      <amexio-btn-group-action [label]="'Default'"></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'primary'"  [type]="'primary'"  ></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'Success'" [type]="'success'" ></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'Danger'" [type]="'danger'" ></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'Warning'" [type]="'warning'" ></amexio-btn-group-action>
</amexio-btn-group>

<!--Button Group States-->

<amexio-btn-group>
      <amexio-btn-group-action [label]="'Disabled'"  [type]="'primary'"  [tooltipMessage]="'Disabled'" [disabled]="'true'"></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'Enabled'" [type]="'primary'" [tooltipMessage]="'Enabled'"></amexio-btn-group-action>
</amexio-btn-group>

<!--Button Group with Icons-->

<amexio-btn-group>
      <amexio-btn-group-action [label]="'Next'" [type]="'primary'" [tooltipMessage]="'Next'" icon="chevron-right"  ></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'Download'" [type]="'success'" [tooltipMessage]="'Save'" icon="save" ></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'Delete'" [type]="'danger'" [tooltipMessage]="'delete'" icon="trash" ></amexio-btn-group-action>
</amexio-btn-group>

<!--Button Group Sizes-->

<!--Button Group with large size-->
<amexio-btn-group [size]="'large'">
      <amexio-btn-group-action [label]="'Button1'" [type]="'primary'" ></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'Button2'" [type]="'primary'"  ></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'Button3'" [type]="'primary'"  ></amexio-btn-group-action>
</amexio-btn-group>
                      
<!--Button Group with small size-->
 
<amexio-btn-group [size]="'small'">
      <amexio-btn-group-action [label]="'Button1'" [type]="'primary'" ></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'Button2'" [type]="'primary'"  ></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'Button3'" [type]="'primary'"  ></amexio-btn-group-action>
</amexio-btn-group>
                   
<!--Button Group with xsmall size-->
                       
<amexio-btn-group [size]="'xsmall'">
      <amexio-btn-group-action [label]="'Button1'" [type]="'primary'" ></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'Button2'" [type]="'primary'"  ></amexio-btn-group-action>
      <amexio-btn-group-action [label]="'Button3'" [type]="'primary'"  ></amexio-btn-group-action>
</amexio-btn-group>
                        
    `
    this.tsCode=`import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'button-group',
  templateUrl: 'buttongroup.demo.html'
})
export class ButtonGroupDemoComponent implements OnInit {
 asyncFlag : boolean;
  constructor() {}
  
  ngOnInit() {
  }
  switchasyncFlag(){
    this.asyncFlag = true;
    setTimeout(() => {
      this.asyncFlag = false
    }, 3000);
  }
}

    `
  }

  ngOnInit() {
  }
  switchasyncFlag(){
    this.asyncFlag = true;
    setTimeout(() => {
      this.asyncFlag = false
    }, 3000);
  }

  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



