import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'dropdown-button',
  templateUrl: 'dropdownbutton.demo.html'
})
export class DropDownButtonComponent implements OnInit {
  tsCode:any;
  isExpanded :boolean=false;
  htmlCode:any;
  constructor() {
    this.tsCode=`import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'dropdown-button',
  templateUrl: 'dropdownbutton.demo.html'
})
export class DropDownButtonComponent implements OnInit {
  constructor() {
  }

  ngOnInit() {
  }
}
    `
    this.htmlCode=`<!--Basic DropDown Button-->
<amexio-btn-dropdown [label]="'Button'" 
        [type]="'success'" [size]="'default'">
        <amexio-btn-dropdown-item [label]="'Link 1'" [icon]="'fullscreen'"></amexio-btn-dropdown-item>
        <amexio-btn-dropdown-item [label]="'Link 2'"  [icon]="'trash'"></amexio-btn-dropdown-item>
</amexio-btn-dropdown>

<!--Dropdown Button with size-->

<div class="col-lg-12">
      <div class="col-lg-4">
        <amexio-btn-dropdown [label]="'Button'" 
            [type]="'primary'" [size]="'large'">
            <amexio-btn-dropdown-item [label]="'Link 1'" ></amexio-btn-dropdown-item>
            <amexio-btn-dropdown-item [label]="'Link 2'" ></amexio-btn-dropdown-item>
        </amexio-btn-dropdown>                     
      </div>
      <div class="col-lg-4">
        <amexio-btn-dropdown [label]="'Button'" 
            [type]="'primary'" [size]="'small'">
            <amexio-btn-dropdown-item [label]="'Link 1'" ></amexio-btn-dropdown-item>
            <amexio-btn-dropdown-item [label]="'Link 2'" ></amexio-btn-dropdown-item>
        </amexio-btn-dropdown>
      </div>
      <div class="col-lg-4">
        <amexio-btn-dropdown [label]="'Button'" 
            [type]="'primary'" [size]="'xsmall'">
            <amexio-btn-dropdown-item [label]="'Link 1'" ></amexio-btn-dropdown-item>
            <amexio-btn-dropdown-item [label]="'Link 2'" ></amexio-btn-dropdown-item>
            </amexio-btn-dropdown>
      </div>
</div>

<!--Dropdown Button with Disebled link-->

<amexio-btn-dropdown [label]="'Disebled Link'" 
      [type]="'primary'" [size]="'default'">
      <amexio-btn-dropdown-item [label]="'Disebled'"  [icon]="'fullscreen'" [disabled]="true"></amexio-btn-dropdown-item>
      <amexio-btn-dropdown-item [label]="'Link 2'" [icon]="'trash'"></amexio-btn-dropdown-item>
</amexio-btn-dropdown>

    `
  }

  ngOnInit() {
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



