import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'email-input',
  templateUrl :'email.demo.html'
})
export class EmailDemoComponent implements OnInit {
  htmlCode : string;
  tsCode : string;
  isExpanded : boolean=false;

  constructor(){
    this.htmlCode=`<!--Simple Email Input-->
                  
<amexio-email-input [fieldLabel]="'Email Id'" name ="email"
                    [placeholder]="'Enter Email Id'"
                    [allowBlank]="false" [errorMsg] ="'Please enter Email Id'"
                    [iconFeedBack]="true">
</amexio-email-input>

 <!--Text Input with Font Style-->
                 
<amexio-email-input [fieldLabel]="'Email Id'" name ="email"
                     [placeholder]="'Enter Email Id'"
                     [iconFeedBack]="true"
                     [fontStyle]="'italic'"
</amexio-email-input>

 <!--Text Input with Font Family-->
                     
<amexio-email-input name ="email" [fieldLabel]="'Email Id'"
                    [placeholder]="'Enter Email Id'"
                    [fontFamily]="'Times New Roman'"
                    [fontSize]="'large'">
</amexio-email-input>

<!--Text Input disabled Property--> 

<amexio-email-input name ="email" [fieldLabel]="'Email Id'"
                    [placeholder]="'Enter Email Id'"
                    [disabled]="true">
</amexio-email-input>

    `;

    this.tsCode=`import {Component, OnInit} from '@angular/core';
    
@Component({
  selector: 'email-input',
  templateUrl :'email.demo.html'
})
export class EmailDemoComponent implements OnInit {
  constructor(){
  }
  ngOnInit() {
  }
  ngAfterViewInit(){
  }
} 
    
    `
  }

  ngOnInit() {
  }

  ngAfterViewInit(){

  }

  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }

  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }

}



