import {AfterViewChecked, ChangeDetectorRef, Component, OnInit} from '@angular/core';
declare var $;
@Component({
  moduleId: module.id,
  selector: 'dropdown-demo',
  templateUrl: 'dropdown.demo.html'
})
export class DropDownDemoComponent implements OnInit,AfterViewChecked {

  sportname : string;
  sportNameObj : any;
  country : string;
  fruit : any[] = [];
  fruit2 : any[] = [];

  htmlCode : string;
  tsCode : string;
  constructor(private ref: ChangeDetectorRef) {
    this.htmlCode =`
    
     <div class="row" id="dropdownexamples">
    <h2 id="examples" class="page-header" style="padding-top: 10%">&nbsp;&nbsp;Example</h2>
    <div class="panel panel-default">
      <div class="panel-heading"><b>Basic Dropdown</b></div>
      <div class="panel-body">
        <div class="row">
          <div class="col-lg-3">
            <div class="form-group">
              <drop-down [(ngModel)]="sportname"
                         [placeholder]="'Choose'"
                         name="sportname"
                         [dataReader]="'response.data'"
                         [fieldLabel]="'Choose Sport'"
                         [httpUrl]="'assets/data/dropdown/sports.json'"
                         [httpMethod]="'get'"
                         [displayField]="'sportName'"
                         [valueField]="'code'"
                         [width]="'250px'"
                          (onSingleSelect)="getSingle($event)">
              </drop-down>
            </div>
          </div>

          <div class="col-lg-3" style="padding-top: 2.2%;">
            <div class="well well-sm">Selected Value: <b>{{sportname}}</b></div>
          </div>
          <div class="col-lg-6" style="padding-top: 2.2%;">
            <div class="well well-sm">Selected Object: <b>{{sportNameObj | json}}</b></div>
          </div>
      </div>
    </div>
  </div>
    <div class="panel panel-default">
      <div class="panel-heading"><b>Filter Search Dropdown</b></div>
      <div class="panel-body">
        <div class="row">
          <div class="col-lg-4">
            <div class="form-group">
              <drop-down [(ngModel)]="country"
                         [placeholder]="'Choose'"
                         name="country"
                         [dataReader]="'response.data'"
                         [fieldLabel]="'Choose Country'"
                         [httpUrl]="'assets/data/country.json'"
                         [httpMethod]="'get'"
                         [searchBox]="'true'"
                         [displayField]="'countryName'"
                         [valueField]="'countryName'"
                         [width]="'auto'">
              </drop-down>
            </div>
          </div>
          <div class="col-lg-3" style="padding-top: 2.2%;">
            <div class="well well-sm">Selected Value: <b>{{country}}</b></div>
          </div>
        </div>
      </div>
    </div>

    <div class="panel panel-default">
      <div class="panel-heading"><b>Multi Select Dropdown</b></div>
      <div class="panel-body">
        <div class="row">
          <div class="col-lg-4">
            <div class="form-group">
              <drop-down  (onMultiSelect)="setMultiVal($event)"
                         [placeholder]="'Choose'"
                         [dataReader]="'response.data'"
                         [fieldLabel]="'Pick Some Fruits'"
                         [httpUrl]="'assets/data/dropdown/fruits.json'"
                         [httpMethod]="'get'"
                         [displayField]="'fruitName'"
                         [valueField]="'code'"
                         [width]="'auto'"
                         [multiSelect]="'true'">
              </drop-down>
            </div>
          </div>
          <div class="col-lg-6">
            <div class="well well-sm">Selected Fruits:
              <div *ngFor="let ft of fruit">
                <p>{{ft.fruitName}}</p>
              </div>
            </div>
          </div>

        </div>
        <div class="row">
          <h4 class="panel-heading">Multi Select With Limited Select</h4>
          <div class="col-lg-6">
            <div class="form-group">
              <drop-down  (onMultiSelect)="setMultiVal2($event)"
                          [placeholder]="'Choose'"
                          [dataReader]="'response.data'"
                          [fieldLabel]="'Pick Two Fruits'"
                          [httpUrl]="'assets/data/dropdown/fruits.json'"
                          [httpMethod]="'get'"
                          [displayField]="'fruitName'"
                          [valueField]="'code'"
                          [width]="'auto'"
                          [multiSelect]="'true'"
                          [maxMultiSelect]="'2'">
              </drop-down>
            </div>
          </div>
          <div class="col-lg-4">
            <div class="well well-sm">Only 2 Fruits: <b>{{fruit2 | json}}</b></div>
          </div>
        </div>
        <div class="row">
          <h4 class="page-header">Count Display Mode</h4>
          <div class="col-lg-6">
            <drop-down  [placeholder]="'Choose'"
                        [dataReader]="'response.data'"
                        [fieldLabel]="'Choose Option'"
                        [httpUrl]="'assets/data/dropdown/fruits.json'"
                        [httpMethod]="'get'"
                        [displayField]="'fruitName'"
                        [valueField]="'code'"
                        [width]="'auto'"
                        [multiSelect]="'true'"
                        [multiCountDisplay]="'count'"
                        >
            </drop-down>
          </div>
        </div>
    </div>
    </div>
  </div>
     
    `;
    this.tsCode=`
import {ChangeDetectorRef, Component, OnInit, AfterViewChecked} from '@angular/core';
declare var $;
@Component({
  selector: 'dropdown-demo',
  templateUrl: 'dropdown.demo.html'
})
export class DropDownDemoComponent implements OnInit,AfterViewChecked { 
  sportname : string;
  sportNameObj : any;
  taste : any;
  country : string;
  fruit : any[] = [];
  fruit2 : any[] = [];
  constructor(private ref: ChangeDetectorRef) {
  }
  ngOnInit() {
  }
  ngAfterViewChecked(){
    $('.selectpicker').selectpicker('render');
  }
  setMultiVal(val : any){
    this.fruit = val.data;
    this.ref.detectChanges();
  }

  setMultiVal2(val : any){
    this.fruit2 = val.data;
    this.ref.detectChanges();
  }
    getSingleVal(value : any){
    this.sportNameObj = value.data;
  }
} 
    `;
  }

  ngOnInit() {
  }

  ngAfterViewChecked(){
    $('.selectpicker').selectpicker('refresh');
  }

  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }

  setMultiVal(val : any){
    console.log(val);
    this.fruit = val.data;
    this.ref.detectChanges();
  }

  setMultiVal2(val : any){
    this.fruit2 = val.data;
    this.ref.detectChanges();
  }

  getSingleVal(value : any){
    this.sportNameObj = value.data;
    console.log(value);
  }
}



