import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'tree-template-demo',
  templateUrl: 'treetemplate.demo.html'
})
export class TreeTemplateComponent implements OnInit {

  htmlCode : string;
  tsCode : string;
  datasource : string;
  data : any;
  isExpanded : boolean=false;
  constructor() {
    this.htmlCode = `
<amexio-tree-view
          [dataReader]="'data'" [httpMethod]="'get'"
          [httpUrl]="'assets/data/treeviewicon.json'"
          (selectedRecord)="onRowSelect($event)">
          <ng-template #amexioTreeTemplate let-tree let-icon="icon" let-node="node">
            <i [attr.class]="node.icon"></i> &nbsp; {{tree.text}}
          </ng-template>
</amexio-tree-view>
    `;

    this.tsCode = `import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'tree-template-demo',
  templateUrl: 'treetemplate.demo.html'
})
export class TreeTemplateComponent implements OnInit {
  constructor(){}
  ngOnInit(){}

}`;

    this.datasource = `{
  "data": [
    {
      "text": "All",
      "expanded": true,
      "checked" : false,
      "icon" : "fa fa-sitemap",
      "children": [
        {
          "text": "Home",
          "expanded": true,
          "checked" : false,
          "icon" : "fa fa-home",
          "children": [
            {
              "leaf": true,
              "checked" : false,
              "text": "Messages",
              "icon" : "fa fa-inbox"
            },
            {
              "leaf": true,
              "checked" : false,
              "text": "Archive",
              "icon" : "fa fa-database"
            },
            {
              "leaf": true,
              "checked" : false,
              "text": "No Icon",
              "icon" : ""
            },
            {
              "leaf": true,
              "checked" : false,
              "text": "Music",
              "icon" : "fa fa-music"
            },
            {
              "leaf": true,
              "checked" : false,
              "text": "Film",
              "icon" : "fa fa-film"
            }

          ]
        },
        {
          "text": "Users",
          "expanded": true,
          "checked" : false,
          "icon" : "fa fa-user",
          "children": [
            {
              "leaf": true,
              "checked" : false,
              "text": "Tagged",
              "icon" : "fa fa-tag"
            },
            {
              "leaf": true,
              "checked" : false,
              "text": "Inactive",
              "icon" : "fa fa-trash"
            }
          ]
        },
        {
          "text": "Groups",
          "expanded": true,
          "checked" : false,
          "icon" : "fa fa-group",
          "children": [
            {
              "leaf": true,
              "checked" : false,
              "text": "Settings",
              "icon" : "fa fa-share-alt"
            },
            {
              "leaf": true,
              "checked" : false,
              "text": "Network",
              "icon" : "fa fa-signal"
            },
            {
              "leaf": true,
              "checked" : false,
              "text": "Lazy Node",
              "expanded" : false,
              "children":[],
              "icon" : "",
              "lazy":{
                "httpUrl":"assets/data/treeview.json",
                "httpMethod": "get"
              }
            }
          ]
        },
        {
          "text": "Travel",
          "expanded": true,
          "checked" : false,
          "icon" : "fa fa-suitcase",
          "children": [
            {
              "leaf": true,
              "checked" : false,
              "text": "Car",
              "icon" : "fa fa-car"
            },
            {
              "leaf": true,
              "checked" : false,
              "text": "Train",
              "icon" : "fa fa-train"
            },
            {
              "leaf": true,
              "checked" : false,
              "text": "Rocket",
              "icon" : "fa fa-rocket"
            }
          ]
        }
      ]
    }]
}
`;
  }

  ngOnInit() {
  }

  onRowSelect(data : any){
    this.data = data;
  }

  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }
  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



