import {Component, OnInit} from '@angular/core';
declare var $;
@Component({
  selector: 'rating-demo',
  templateUrl : 'rating.demo.html'
})
export class RatingDemoComponent implements OnInit {

  public max:number = 10;
  public rate:number = 7;
  public isReadonly:boolean = false;
  public isPercent:boolean = true;
  isExpanded :boolean=false;


  htmlCode : string;
  tsCode : string;

  constructor() {
    this.htmlCode = `
<amexio-rating-input [(ngModel)]="rate" 
         [max]="max" name="rate"
         [readonly]="isReadonly"
         [percentageLabel]="isPercent">                                
</amexio-rating-input>`;

    this.tsCode = `import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'rating-demo',
  templateUrl : 'rating.demo.html'
})
export class RatingDemoComponent implements OnInit {

  public max:number = 10;
  public rate:number = 7;
  public isReadonly:boolean = false;
  public isPercent:boolean = true;
  
  constructor() {}
  ngOnInit() {}
}
    `
  }

  ngOnInit() {
  }
  scrollTo(loc : string){
    $('html, body').animate({
      scrollTop: $('#'+loc).offset().top
    }, 500);
  }

  toogle(){
    this.isExpanded =!this.isExpanded;
    if(this.isExpanded){
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }else {
      $("#sidebar").toggleClass("collapsed");
      $("#content").toggleClass("col-md-12 col-md-6");
    }

  }
}



